import React, { useState } from 'react';
import Youtube from './Youtube';
import Accordion from 'react-bootstrap/Accordion';
import { useAccordionButton } from 'react-bootstrap/AccordionButton';
import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';

export default function Projects() {
    const [demoVisible, setDemoVisible] = useState(false);
    function CustomToggle({ children, eventKey }) {
        const decoratedOnClick = useAccordionButton(eventKey, () =>
          setDemoVisible(!demoVisible)
        );
      
        return (
            <Button variant="warning" onClick={decoratedOnClick}>{children}</Button>
        );
    }
    return (
        <div>
            <Container>
                <Accordion>
                <Card bg="dark" text="white">
                    <Card.Header>
                        <h3>SellFast@Cal (CS169)</h3>
                    </Card.Header>
                    <Card.Body>
                        <p> My first experience with creating a full-stack application using Ruby on Rails and HTML+CSS+Bootstrap. 
                            While the application is no longer able to be viewed online, you can watch a video of the final application demo 
                            by clicking on the button below.
                        </p>
                        <CustomToggle eventKey="0">{demoVisible ? "Hide Demo" : "Show Demo"}</CustomToggle> 
                    <Accordion.Collapse eventKey="0">
                    
                        
                        <Youtube videoID="sc1xSYAEcMI" />
                    </Accordion.Collapse>
                    </Card.Body>
                    
                </Card>
                </Accordion>
            </Container>
        </div>
    )
};
